import {
    collection,
    db,
    getDocs,
} from "../../../service/firebaseConfig";

export async function getRifa() {
    try {
        const rifaCollection = collection(db, "Rifa");
        const querySnapshot = await getDocs(rifaCollection);
        // Mapear todos los documentos a un array con sus datos + id (opcional)
        const rifaData = querySnapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data(),
        }));

        return rifaData;
    } catch (error) {
        console.error(
            " Error al guardar la entrada y actualizar el contador:",
            error
        );
        return false; // Retornamos false en caso de error
    }
}
