import React, { useEffect, useState } from "react";



import { Loading } from "../../../components/loading/loading.tsx";
import { useNavigate } from "react-router-dom";
import { createRifaService } from "./createRifa.service.tsx";
import FormRifa from "../../../components/form/formRifa/formRifa.tsx";
import { getRifa } from "../../public/rifa/rifa.service.tsx";
import RifaTable from "../../../components/rifaTable/rifaTable.tsx";

export const CreateRifa = () => {
    const navigate = useNavigate();
    const [showLoading, setShowLoading] = useState(false);
    const [showAlert, setShowAlert] = useState(false);
    const [menssageAlert, setMenssageAlert] = useState('');


    const setDataRifa = (data: any) => {
        setShowLoading(true);
        console.log("data", data)
        createRifaService(data)
            .then((result) => {
                if (result !== false) {
                    setShowLoading(false);
                    window.location.reload();
                } else {
                    setMenssageAlert("Rifa ya registrada")
                    setShowLoading(false);
                    setShowAlert(true)

                    window.scrollTo({
                        top: document.body.scrollHeight,
                        behavior: 'smooth'
                    });
                }
            })
            .catch((error) => {
                console.error("Error al crear ticket:", error);
                setShowLoading(false);
            });
    };

    const [dataRifaData, setDataRifaData] = useState([]);
    useEffect(() => {
        setShowLoading(true);
        getRifa()
            .then((result) => {
                window.scrollTo({
                    top: document.body.scrollHeight,
                    behavior: 'smooth'
                });
                console.log("result", result)
                setDataRifaData(result)
                    setShowLoading(false);
            })
            .catch((error) => {
                console.error("Error al crear ticket:", error);
                setShowLoading(false);
            });

    }, []);

    return (
        <>
            <Loading loading={showLoading} />
            <div className="container">
                <div className="section2 d-flex justify-content-center align-items-center position-relative">
                    <FormRifa onSubmit={(e) => setDataRifa(e)} menssageAlert={menssageAlert} showAlert={showAlert} />
                </div>

                <div className=" d-flex justify-content-center align-items-center position-relative">
                    {dataRifaData.length !== 0 && <RifaTable dataRifa={dataRifaData} />}


                </div>
            </div>
        </>
    );
};
