import React, { useEffect, useState } from "react";
import { getAllTicket } from "./showAllTickets.service.tsx";
import { Loading } from "../../../components/loading/loading.tsx";
import { Button, Row, Table, Form } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

export const ShowAllTickets = () => {
    const navigate = useNavigate();
    const [showLoading, setShowLoading] = useState(false);
    const [dataUsers, setDataUsers] = useState<any[]>([]);
    const [searchText, setSearchText] = useState("");

    useEffect(() => {
        const fetchData = async () => {
            setShowLoading(true);
            const result = await getAllTicket();
            setDataUsers(result);
            setShowLoading(false);
        };

        fetchData();
    }, []);

    //  Filtro por nombre (insensible a maysculas)
    const filteredUsers = dataUsers.filter((ticket) =>
        ticket.name.toLowerCase().includes(searchText.toLowerCase())
    );

    return (
        <>
            <Loading loading={showLoading} />
            <div className="container">
                <Form.Group className="mb-3" controlId="search">
                    <Form.Control
                        type="text"
                        placeholder="Buscar por nombre..."
                        value={searchText}
                        onChange={(e) => setSearchText(e.target.value)}
                    />
                </Form.Group>

                {filteredUsers.length > 0 ? (
                    <Table striped bordered hover responsive>
                        <thead>
                            <tr>
                                <th>Nombre</th>
                                <th>Telfono</th>
                                <th>VIP</th>
                                <th>Asistencia</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filteredUsers.map((ticket, index) => (
                                <tr key={index}>
                                    <td>{ticket.name}</td>
                                    <td>{ticket.phone}</td>
                                    <td>{ticket.vip ? "S" : "No"}</td>
                                    <td>{ticket.assistance ? "S" : "No"}</td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                ) : (
                    <p>No hay resultados para tu bsqueda.</p>
                )}

                <Row>
                    <Button
                        onClick={() => navigate('/privateHome')}
                        className="buttonBack"
                        variant="outline-info"
                    >
                        Volver
                    </Button>
                </Row>
            </div>
        </>
    );
};
