import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { Alerts } from "../../alerts/alerts.tsx";
import { useNavigate } from "react-router-dom";


const FormRifa = ({ onSubmit, menssageAlert, showAlert }) => {
  const navigate = useNavigate();
  const [validated, setValidated] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    id: "",

  });

  const handleChange = (event) => {
    const { name, value } = event.target;


    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent page reload
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    } else {
      onSubmit(formData);
    }
    setValidated(true);
  };

  return (
    <div className="containterForm">
      <Row>
        <Col xs={12} className="colTitle">
          <h3 className="titleForm">Crear Rifa</h3>
        </Col>
      </Row>

      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Row className="mb-3">
          <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom01">
            <Form.Label className="label">Numero</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Numero"
              name="id"
              value={formData.id}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} xs="6" sm="4" controlId="validationCustom02">
            <Form.Label className="label">Nombre</Form.Label>
            <Form.Control
              className={"campo"}
              required
              type="text"
              placeholder="Nombre"
              name="name"
              value={formData.name}
              onChange={handleChange}
            />
            <Form.Control.Feedback type="invalid">
              Campo requerido
            </Form.Control.Feedback>
          </Form.Group>
        </Row>

        <Form.Group as={Col} xs="6" sm="4" className="rowButton">
          <Button
            className="buttonSend"
            variant="outline-warning"
            type="submit"
          >
            Crear
          </Button>
        </Form.Group>
        <Form.Group as={Col} xs="6" sm="4" className=" rowButton">
          <Button onClick={() => navigate('/privateHome')} className="buttonBack" variant="outline-info" >
            Volver
          </Button>
        </Form.Group>
        
        {showAlert && (
          <Form.Group>
            <Alerts message={menssageAlert} />
          </Form.Group>
        )}
      </Form>
    </div>
  );
};

export default FormRifa;
