import React, { useEffect, useState } from "react";
import ImageCarousel from "../../../components/carousel/imageCarousel.tsx";
import { Header } from "../../../components/header/header.tsx";
import { Row } from "react-bootstrap";
import { Accordion } from "react-bootstrap";
import CountdownTimer from "../../../components/countdowTimer/countdowTimer.tsx";
import AccordionComponent from "../../../components/accordion/accordionComponent.tsx";
import { Menu } from "../../../components/menu/menu.tsx";
import RifaTable from "../../../components/rifaTable/rifaTable.tsx";
import { getRifa } from "./rifa.service.tsx";



export const Rifa = () => {
    const [showLoading, setShowLoading] = useState(false);
    const targetDate = "2025-07-19T23:59:59";
    const images = [
        require("../../../media/images/cartelRifa.png"),
        require("../../../media/images/RIFA.png"),

    ];
    const [dataRifa, setDataRifa] = useState([]);
    useEffect(() => {
        setShowLoading(true);
        getRifa()
            .then((result) => {
                window.scrollTo({
                    top: document.body.scrollHeight,
                    behavior: 'smooth'
                });
                console.log("result", result)
                setDataRifa(result)
            })
            .catch((error) => {
                console.error("Error al crear ticket:", error);
                setShowLoading(false);
            });

    }, []);

    return (
        <>
            <div className="container">
                <div className="section1 d-flex justify-content-center align-items-center position-relative">
                    <Menu />
                    <Header />
                    <ImageCarousel />
                    <CountdownTimer targetDate={targetDate} />
                </div>
                <div className="section2 ">
                    <div className="bodyAccordion">
                        <h3 className="title">Trminos de condiciones</h3>
                        <Accordion >
                            <Accordion.Item eventKey="0">
                                <Accordion.Header>Rifa</Accordion.Header>
                                <Accordion.Body>
                                    <p>
                                        Se puede abonar el pago mediante Bizum o en efectivo
                                    </p>
                                    <p>
                                        Si el pago ha sido realizado por Bizum en el concepto debes de indicar el nmero
                                    </p>
                                    <p>
                                        No se podr reservar nmero si no ha realizadoelpago
                                    </p>
                                    <p>Durante el proceso de compra de un nmero, si ese nmero ha sido comprado por otro usuario o se ha adelantado, se comprueba la hora de la actividad, y se lo quedar el usuario que lo compr primero</p>
                                </Accordion.Body>
                            </Accordion.Item>
                            <Accordion.Item eventKey="1">
                                <Accordion.Header>Cmo se sortea?</Accordion.Header>
                                <Accordion.Body>
                                    <p>
                                        Durante la noche se anunciar en la fiesta a travs de un sistema aleatorio
                                    </p>
                                    <p>
                                        Primero se sortea el segundo premio y despus el primero
                                    </p>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </div>
                </div>


                <div className=" d-flex justify-content-center align-items-center position-relative">
                    <Row>

                        <img
                            className=""
                            src={images[0]}
                            alt={`Image ${0 + 1}`}
                            style={{ opacity: 1 }}
                        />
                    </Row>

                </div>
                <div className=" d-flex justify-content-center align-items-center position-relative">
                    {/* <Row>

                        <img
                            className=""
                            src={images[1]}
                            alt={`Image ${0 + 1}`}
                            style={{ opacity: 1 }}
                        />
                    </Row> */}
                    {dataRifa.length!==0&&     <RifaTable dataRifa={dataRifa} />}
                 

                </div>



            </div>
        </>
    );
};