import {
  addDoc,
  collection,
  db,
  getDocs,
  query,
  where,
} from "../../../service/firebaseConfig";

export async function createRifaService(data: any) {
  try {
    const q = query(
      collection(db, "Rifa"), // Reemplaza 'Entradas' con el nombre correcto de la coleccin
      where("id", "==", parseFloat(data.id)) // Filtrar por el campo idQR
    );
    const queryPhone = await getDocs(q);
    if (queryPhone.empty) {
      // Crear el ticket en la coleccin "Entradas"
      const entradasCollection = collection(db, "Rifa");

      await addDoc(entradasCollection, {
        name: data.name,
        id: parseFloat(data.id)

      });


      return true; // Retornar true si todo fue exitoso
    } else {
 
      return false;
    }
  } catch (error) {
    console.error(
      " Error al guardar la entrada y actualizar el contador:",
      error
    );
    return false; // Retornamos false en caso de error
  }
}
