import React, { useState, useEffect } from "react";



// Generador inicial de la rifa
const generarRifa = () =>
    Array.from({ length: 101 }, (_, i) => ({
        id: i,
        titulo: `${i}`,
        name: "",
        estado: ""
    }));

// Mapeo de colores segn estado
const estadoColor = {
    libre: "bg-green-100 border-green-500",
    reservado: "bg-yellow-100 border-yellow-500",
    pagado: "bg-blue-100 border-blue-500"
};

export default function RifaTable({dataRifa}) {
    const [rifa, setRifa] = useState([] as any);

    useEffect(() => {
        // Generar rifa base
        const base = generarRifa();

        // Actualizar con los datos del servicio
        const actualizada = base.map((casilla) => {
            const encontrado = dataRifa.find((d) => d.id === casilla.id);
            if (encontrado) {
                return {
                    ...casilla,
                    name: encontrado.name,
                    estado: "reservado" // Puedes cambiar a "pagado" si aplica
                };
            }
            return casilla;
        });

        setRifa(actualizada);
    }, []);
 return (
    <div className="p-4 overflow-x-auto">
      <div
        className="grid gap-1"
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(10, 60px)", // 10 columnas fijas
        }}
      >
        {rifa.map((casilla) => (
          <div
            key={casilla.id}
             style={{
             height: "3.5rem",
             color:'white'
      
        }}
            className={`border text-center text-xs font-medium h-16 w-[60px] rounded flex flex-col justify-center ${
              estadoColor[casilla.estado] || "bg-gray-100"
            }`}
          >
            <div className="text-sm font-bold">{casilla.titulo}</div>
            <div className="text-[10px] truncate">{casilla.name}</div>
          </div>
        ))}
      </div>
    </div>
  );
}
